<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="t_bar">
          <yd-flexbox class="t_bar_num">
              <yd-flexbox-item>
                <span class="t_bar_num_top">{{$t('tuiguang.friend')}}</span>
                <span class="t_bar_num_bottom">{{list.totalCount}}</span>
              </yd-flexbox-item>
              <yd-flexbox-item>
                <span class="t_bar_num_top">{{$t('tuiguang.jiangli')}}</span>
                <span class="t_bar_num_bottom">{{list.reward}}</span>
              </yd-flexbox-item>
          </yd-flexbox>
        </div>
        <div class="t_addr">
          <yd-flexbox class="t_addr_list">
              <yd-flexbox-item>{{$t('tuiguang.address')}}</yd-flexbox-item>
              <yd-flexbox-item>{{$t('tuiguang.reward')}}</yd-flexbox-item>
              <yd-flexbox-item>{{$t('tuiguang.riqi')}}</yd-flexbox-item>
          </yd-flexbox>

          <yd-flexbox class="t_addr_data" v-for="item in list.extensionRecords">
              <yd-flexbox-item>{{item.publicKey}}</yd-flexbox-item>
              <yd-flexbox-item>{{item.reward}}</yd-flexbox-item>
              <yd-flexbox-item>{{item.createTime}}</yd-flexbox-item>
          </yd-flexbox>
        </div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
export default {
  beforeCreate () {
    var url = this.$route.path
    setTimeout(function(){
      document.querySelector('body').setAttribute('style', 'background:#ffffff')
    },10)
  },
  components:{
      "headerBar":headerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.extension'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      list:''
    }
  },
  created(){
    this.team()
  },
  methods:{
    team(){
       this.openloading(this.$t('toast.loding'))
       var that = this;
       this.axios({
         url:this.Url + 'Extension.ashx',
         method:'post',
         params: {req:6,publickey:getCookie("publickey")}
       }).then(function (res) {
         that.closeloading()
           var _list = res.data.data;
           if (_list) {
            for (var i = 0; i < _list.extensionRecords.length; i++) {
              _list.extensionRecords[i].createTime = _list.extensionRecords[i].createTime.split("T")[0]
              if(_list.extensionRecords[i].publicKey){
                var aa = _list.extensionRecords[i].publicKey.substring(0,6)
                var bb = _list.extensionRecords[i].publicKey.substring(36,42)
                _list.extensionRecords[i].publicKey = aa+"..."+bb
              }else{
                _list.extensionRecords[i].publicKey = that.$t('data.Invitedaward')
              }
              
            }
             that.list = _list
           }
       })

    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.t_bar{
  margin: .42rem .2rem;
  box-shadow: 0 5px 10px #a9c2d8;
  background: #fff;
  text-align: center;
  border-radius: 6px;
  padding: .2rem 0;
}
.t_bar_num .yd-flexbox-item:nth-child(1){
  border-right: 1px solid #f4f4f4;
}
.t_bar_num_top{
  font-size: .3rem;
  color: #333;
  display: block;
  margin-top: .1rem;
  margin-bottom: .2rem;
}
.t_bar_num_bottom{
  font-size: .28rem;
  color: #278eed;
  display: block;
  margin-top: .2rem;
  margin-bottom: .1rem;
}
.t_addr{
  background: #fff;
  text-align: center;
}
.t_addr_list{
  border-bottom: 1px solid #f4f4f4;
  padding: .25rem 0;
  font-size: .28rem;
}
.t_addr_data{
  padding: .25rem 0;
  font-size: .26rem;
  color: #888
}
</style>



